.wrapper {
  position: absolute;
  top: 80px;
  width: 300px;
  left: 5%;
  font-size: 30px;
  height: 175px;
  text-align: left;
  color: #999999;
  animation: color 1.5s infinite;
  filter: blur(0px);
  transition: 2.5s;
  &:hover {
    filter: blur(8px);
    transition: 2.5s; } }

.bloclink {
  position: absolute;
  top: 100px;
  width: 480px;
  right: 15%;
  opacity: 1;
  animation-name: flash;
  -webkit-animation-name: flash;
  -moz-animation-name: flash;
  -o-animation-name: flash;
  -webkit-animation-duration: 10s;
  -moz-animation-duration: 10s;
  -o-animation-duration: 10s;
  animation-duration: 10s;


  @keyframes flash {
    0%, 50% {
      opacity: 0; }
    100% {
      opacity: 1; } } }



.boxlogo {
  display: block;
  float: left;
  width: 80px;
  height: 100px;
  margin-left: 20px;
  margin-right: 20px;
  background-repeat: no-repeat;
  background-size: contain;
  &:hover {
      filter: hue-rotate(270deg); }

  &:nth-last-child(4) {
    background-image: url(../img/cv.svg); }
  &:nth-last-child(3) {
    background-image: url(../img/githab.svg); }
  &:nth-last-child(2) {
    background-image: url(../img/linkedin.svg); }
  &:nth-last-child(1) {
    background-image: url(../img/codepen.svg); }
  a {
    display: block;
    height: 100%;
    vertical-align: bottom;
    color: cyan;
    font-family: Roboto, sans-serif;
    padding-top: 90px; } }


.my_litle_tech {
  position: absolute;
  top: 400px;
  left: 5%;
  width: 550px;
  height: 300px;
  border-top: 1px solid red;
  padding-top: 15px;
  opacity: 1;
  animation-name: flash;
  -webkit-animation-name: flash;
  -moz-animation-name: flash;
  -o-animation-name: flash;
  -webkit-animation-duration: 12s;
  -moz-animation-duration: 12s;
  -o-animation-duration: 12s;
  animation-duration: 12s;
  p {
    margin-top: -45px;
    margin-left: 5px;
    line-height: 2;
    text-align: left; }
  .proglogo {
    width: 127px;
    height: 70px;
    display: block;
    margin-left: 5px;
    margin-right: 5px;
    text-align: left;
    float: left;
    &:hover {
      filter: hue-rotate(270deg); } } }

.descriptbox {
  position: absolute;
  overflow:hidden {}
  display: block;
  right: 10%;
  top: 380px;
  width: 550px;
  height: 230px;
  opacity: 1;
  animation-name: flash;
  -webkit-animation-name: flash;
  -moz-animation-name: flash;
  -o-animation-name: flash;
  -webkit-animation-duration: 14s;
  -moz-animation-duration: 14s;
  -o-animation-duration: 14s;
  animation-duration: 14s;
  article {
    padding-bottom: 5px;
    border-bottom: 1px solid red;
    p {
      font-size: 18px;
      text-align: justify;
      line-height: 1.4; } }
  address {
    padding-top: 8px;
    text-align: left;
    h2 {
      font-size: 16px; }
    p {
      line-height: 1.5; }
    a {
      line-height: 2;
      color: #fc97ee; } } }

@media screen and (max-width: 1200px) {
  .wrapper {
    left: auto;
    display: block;
    position: relative;
    height: 275px;
    width: 500px;
    margin: 0 auto; }
  .bloclink {
    right: auto;
    top: auto;
    height: 130px;
    display: block;
    position: relative;
    margin: 0 auto;
    margin-bottom: 50px; }

  .my_litle_tech {
    left: auto;
    top: auto;
    display: block;
    position: relative;
    margin: 0 auto;
    height: 250px; }


  .descriptbox {
    display: block;
    position: relative;
    right: auto;
    top: auto;
    margin: 0 auto; } }

@media screen and (max-width: 600px) {
  .wrapper {
    font-size: 18px;
    padding: 20px;
    width: 100%; }

  .bloclink {
    height: 270px;
    width: 240px; }

  .boxlogo {
    margin-bottom: 40px; }
  .my_litle_tech {
    padding: 20px;
    height: 420px;
    width: 100%;
    .proglogo {
      margin: auto!important;
      padding: auto!important; } }



  .descriptbox {
    padding: 20px;
    width: 100%; } }






/* The Modal (background) */
.modal {
  display: none /* Hidden by default */;
  position: fixed /* Stay in place */;
  z-index: 1 /* Sit on top */;
  padding-top: 50px /* Location of the box */;
  left: 0;
  top: 0;
  width: 100% /* Full width */;
  height: 100% /* Full height */;
  overflow: auto /* Enable scroll if needed */;
  background-color: rgb(0,0,0) /* Fallback color */;
  background-color: rgba(0,0,0,0.4) /* Black w/ opacity */; }


/* Modal Content */
.modal-content {
  font-family: Ubuntu, sans-serif;
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  width: 800pt;
  height: 1050pt;
  border: 1px solid #888;
  // width: 80%
  color: black; }


/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold; }


.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer; }

